.header2 {
  width: 100%;
  background: rgba(0, 0, 0, 0.5);
  height: 50vh;
  position: relative;
}

.header2::before {
  content: "";
  background: url("https://images.unsplash.com/photo-1484417894907-623942c8ee29?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1332&q=80");
  background-size:contain;
  background-position: center ;
  object-fit: cover;
  height: 50vh;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
}

.heading {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.heading h1{
    font-size: 3.4rem;
}

.heading p{
    font-size: 2.4rem;
    text-align: center;
}

@media screen and (max-width:1040px) {
    .heading h1{
        font-size: 2rem;
    }

    .heading p{
      font-size: 1.4rem;
     
  }
  
}
